.comment {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 20px;
    margin: 10px;
    border-bottom: 2px solid #5b5b5b;
  }
  
  .header div {
    margin: 2px 0;
  }
  
  .author {
    color: #c4c5c5;
    font-weight: bold;
    margin-right: 10px;
  }
  
  .date {
    font-size: 12px;
    color: #c4c5c5;
    font-weight: bold;
  }
  
  .commentText {
    font-size: 18px;
    padding: 5px 0;
  }